@font-face {
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/titan-one-v8-latin-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
    url('../fonts/titan-one-v8-latin-regular.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('../fonts/titan-one-v8-latin-regular.woff2')
      format('woff2'),
    /* Super Modern Browsers */ url('../fonts/titan-one-v8-latin-regular.woff')
      format('woff'),
    /* Modern Browsers */ url('../fonts/titan-one-v8-latin-regular.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('../fonts/titan-one-v8-latin-regular.svg#TitanOne') format('svg'); /* Legacy iOS */
}
/* dm-sans-regular - latin */
